.container {
  max-width: 960px;
  margin: auto;
  padding: 0 20px;
  overflow: auto;
}

.card {
  background: #fff;
  border-radius: 10px;
  margin-bottom: 20px;
  transition: all .3s;
  box-shadow: 0 4px 8px #0003;
}

.card:hover {
  box-shadow: 0 8px 16px #0003;
}

.card-body {
  padding: 20px;
}

.btn {
  color: #fff;
  text-align: center;
  cursor: pointer;
  border: none;
  margin: 4px 2px;
  padding: 15px 32px;
  font-size: 16px;
  text-decoration: none;
  transition-duration: .4s;
  display: inline-block;
}

.btn-primary {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #008cba;
  border: none;
  text-decoration: none;
  transition-duration: .4s;
  display: inline-block;
}

.btn-primary:hover {
  color: #fff;
  background-color: #008cba;
  box-shadow: 0 12px 20px #0000003d;
}

.btn-warning {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #f0ad4e;
  border: none;
  text-decoration: none;
  transition-duration: .4s;
  display: inline-block;
}

.btn-warning:hover {
  color: #fff;
  background-color: #f0ad4e;
  box-shadow: 0 12px 20px #0000003d;
}

.btn-danger {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #d9534f;
  border: none;
  text-decoration: none;
  transition-duration: .4s;
  display: inline-block;
}

.btn-danger:hover {
  color: #fff;
  background-color: #d9534f;
  box-shadow: 0 12px 20px #0000003d;
}

.form-control {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

.form-check-input {
  margin-right: 10px;
}

.form-check-label, h1, h2 {
  color: #333;
}
